<template>
  <div class="real-time-warning">
    <SmallModule title="实时告警">
      <!-- <template #titleRight>
        <div class="detail-text"><span class="text">详情</span><span class="arrow">&gt;</span></div>
      </template> -->
      <template #content>
        <div class="content-layout">
          <a-table
            class="operation-screen-table"
            :columns="realTimeColumns"
            :data-source="rankData"
            size="small"
            :pagination="false"
            :scroll="{ y: '2.24rem' }"
            :row-class-name="(_record, index) => (index % 2 === 0 ? 'table-striped' : null)"
          >
            <template #bodyCell="{ column, record }">
              <template v-if="column.key === 'warningHour'">
                <span class="green-text">{{ record.warningHour }}</span>
              </template>
            </template>
          </a-table>
        </div>
      </template>
    </SmallModule>
  </div>
</template>

<script lang="ts" setup>
  import SmallModule from '/@/components/Framework/OperationScreen/SmallModule.vue';
  import { realTimeColumns, getRealTimeAlarmData } from './data';
  import { reactive, UnwrapRef, onMounted, ref } from 'vue';
  import { assign } from 'lodash-es';
  //搜索参数
  const searchInfo: UnwrapRef<any> = reactive({});
  interface Props {
    monitorSearchInfo?: {};
  }
  const props = withDefaults(defineProps<Props>(), {});

  const rankData = ref([]);

  onMounted(() => {
    assign(searchInfo, props.monitorSearchInfo);
    queryRealTimeAlarmData();
  });

  const queryRealTimeAlarmData = async () => {
    let data = await getRealTimeAlarmData(searchInfo);
    if (data.result) {
      rankData.value = data.result.dataList;
    }
  };
</script>

<style lang="less" scoped>
  .detail-text {
    padding: 0.2rem 0.16rem 0 0;
    color: #fff;
    font-size: 0.12rem;
    line-height: 1;
    cursor: pointer;

    .text {
      margin-right: 0.04rem;
    }

    .arrow {
      opacity: 0.6;
    }
  }

  .content-layout {
    height: 100%;
    margin-top: -0.15rem;
    padding: 0 0.16rem;

    .green-text {
      display: inline-block;
      color: #00ffa4;
    }

    .operation-screen-table {
      height: 100%;

      :deep(.ant-empty-normal) {
        margin: 5.38vh 0;
      }
    }
  }
</style>
